html {
  background: linear-gradient(to left, #223740, #111613);
}

body {
  margin: 0;
}

.wrapper {
  padding: 0;
  margin: 0;
}

.header {
  padding: 50px 20px;
}

.hat {
  display: flex;
  border-bottom: 3px solid white;
}

.logoImg {
  height: 60px;
  width: 100px;
}

.logo {
  padding-left: 10px;
  color: white;
  font-family: sans-serif;
}

.mainImg {
  height: auto;
  width: 95vw;
}

.list {
  width: 300px;
  list-style: none;
  margin: 0 auto;
  padding: 0;
  text-align: center;
}

.listItem {
  margin-bottom: 40px;
}

.title {
  font-family: sans-serif;
  text-align: center;
  font-size: 24px;
  font-weight: 600;
  color: white;
}

.time {
  background-image: url('./images/tires.jpg');
  padding: 50px;
}

.timeInfo {
  color: white;
  font-weight: 600;
  font-size: 20;
  font-family: sans-serif;
  text-align: center;
  line-height: 1.6;
}

.contacts {
  margin-top: 100px;
  margin-bottom: 100px;
  text-align: center;
  color: white;
  list-style: none;
  display: flex;
  justify-content: space-around;
  padding: 0;
}

.contactListItem {
  width: 30vw;
  display: flex;
  flex-direction: column;
}

.icon {
  display: block;
  height: 50px;
  width: 50px;
  margin: 0 auto;
}

.contactTitle::before {
  display: block;
  content: '';
  height: 3px;
  width: 50%;
  margin: 0 auto;
  margin-top: 10px;
  margin-bottom: 10px;
  background-color: rgb(175, 238, 50);
}

.map {
  height: 150px;
  width: 300px;
  line-height: 150px;
  margin: 0 auto;
  border: 1px solid rgb(34, 34, 124);
  text-align: center;
  color: rgb(34, 34, 124);
}

.footer {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  width: 100%;
  text-align: center;
}

@media (min-width: 768px) {
  .header {
    justify-content: start;
  }

  .mainImg {
    width: 98.9vw;
  }

  .list {
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }

  .listItem {
    width: 28%;
  }

  .time {
    padding: 150px;
  }

  .footer {
    /* position: fixed; */
    margin: 0;
    padding: 0;
    bottom: 20px;
    right: 30px;
    flex-direction: row;
    justify-content: flex-end;
    align-self: flex-end;
    flex: 0 0 100%;
  }
}
